// Ear Training for Ukulele Players
// Reference: https://imusicmate.com/ukulele-fretboard.html
import * as Tone from 'tone';
import { Note } from 'tonal';
import { Fretboard, ukulele } from 'traste';


let fret_limit = 3;
let note_sequence = [];

// Create a synth and connect it to the main output (your speakers)
const synth = new Tone.Synth().toDestination();

// Create and draw empty fretboard
const fretboard = new Fretboard(ukulele);
fretboard.draw('#fretboard');


// Events
document.getElementById('play')?.addEventListener('click', async () => {
    fretboard.g.notes.selectAll('*').remove();
    if (!note_sequence.length) {
        note_sequence = getSequence();
    }
    await play();
});


document.getElementById('solution')?.addEventListener('click', async () => {
    await play();
    for (const note of note_sequence) {
        fretboard.drawNoteAtPosition(note.letter, note.string_idx, note.fret_idx);
    }
    // Make sure new sequence will be created when play is clicked again.
    note_sequence = [];
    // TODO: make solution button inactive
});


// Functions
function getSequence() {
    return ukulele.tuning.map((root, string_idx) => {
        const fret_idx = randomInt(fret_limit);
        const note = Note.fromMidi(Note.get(root).midi + fret_idx);
        return {
            note: note,
            letter: Note.get(note).letter,
            string_idx: string_idx,
            fret_idx: fret_idx
        }
    });
}


async function play() {
    const now = Tone.now();
    for (obj of note_sequence) {
        synth.triggerAttackRelease(obj.note, '4n', now + (1 * obj.string_idx));
    }
    await Tone.start();
}


function randomInt(max) {
    return Math.floor(Math.random() * max);
}